import React from 'react';
import { Link } from 'gatsby';
import Img from 'gatsby-image';
import styled from '@emotion/styled';
import PropTypes from 'prop-types';
import theme from '../../config/theme';
import strasua from '../../static/font/strasua.ttf';

const Wrapper = styled.article`

  margin-bottom: 3rem;
  margin-left: 0.2rem;
  margin-right: 0.2rem;
  position: relative;
  border-radius: 0.3rem;
  //box-shadow: ${props => props.theme.shadow.feature.small.default};
  transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  
  flex-basis: calc(99.9% / 4 - 0.5rem);
  flex-grow: 1;
  min-height: 30rem;
  width: calc(99.9% * 1 / 1 - 2.5rem);
  max-width: 25%;

  &:hover {
    transform: scale(1.03);
  }
@media (max-width: 1600px) {
     max-width: 40%;
  }
  @media (max-width: 1400px) {
    flex-basis: calc(99.9%   / 4);
    
     max-width: 33%;
  }

  @media (max-width: 700px) {
    flex-basis: 100%;
    min-height: 100%;
    margin-bottom: 0.5rem;
    max-width: 100%;

  }
`;

const StyledLink = styled(Link)`
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 0.3rem;
  
    transition: opacity 0.25s;
  }
`;

const Image = styled.div`
  position: absolute;
  top: 0;
  overflow: hidden;
  right: 0;
  left: 0;
  bottom: 0;
  object-fit: cover;

  border-radius: 1.5rem;
  img {
    border-radius: 1.5rem;
  }
  > div {
    position: static !important;
  }
  > div > div {
    position: static !important;
  }
`;

const Title = styled.h2`
color: #f0f0f0;
  text-align: center;
  position: relative;
  padding-top: 1.6em;
  //margin-left: auto;
    //margin-right: auto;  
    font-family: Oswald,-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
    font-size: 1.8em;
    font-weight: 600;
`;

const Info = styled.div`
    
    &:after {
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 1.5rem;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.4) 0%,
        rgba(0, 0, 0, 0.3) 50%,
        rgba(0, 0, 0, 0.7) 80%,
        rgba(0, 0, 0, 1.8) 100%
      );
  
`;


const Benefits = styled.div`
  color: #f0f0f0;
  display:flex;
  position: relative;
  font-size:1.2rem;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  font-weight: 300;
  padding-bottom:2rem;
  text-align: left;
`;

const Price = styled.div`
  color: #98ffcc;
  font-style: ${strasua};
  font-weight: 800;
  position: relative;
    text-align: center;

    font-size: 2em;
`;

const Description = styled.div`
  color: #f0f0f0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;

  position: relative;

  padding: 2rem;
    @media (max-width: 700px) {
    display: none;
    padding: 0rem;
  }

`;


const PostList = ({ cover,main_image, path, date,spaces_count, title, excerpt, inf2, benefits, inf3 }) => (

  <Wrapper>
    <Image>
      <Img fluid={cover} />
    </Image>
    <Info/>
    <StyledLink to={path.startsWith('z') ? inf3 : path}>

      <Title>
        {/* <span>{date}</span> */}
        <Img fluid={main_image} /> 
      </Title>

      {/* <Price>
        {inf3}
      </Price> */}

      <Description>
      <Benefits>{inf2}</Benefits>
        
        
          {benefits &&
            benefits.map(tag => {
              const upperTag = tag;
              return (
                // 
                <li>{upperTag}</li>
                // 
              );
            })}
        
      
      </Description>

      {/* <span>{inf3}</span> */}

    </StyledLink>
  </Wrapper>
);

export default PostList;

PostList.propTypes = {
  cover: PropTypes.object.isRequired,
  main_image: PropTypes.object.isRequired,
  path: PropTypes.string.isRequired,
  inf2: PropTypes.string,
  inf3: PropTypes.string,
  benefits: PropTypes.array,
  spaces_count: PropTypes.string,
  date: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};
